<template>
  <div>
    <div id="stockpot-area" @click="IsShowRecipe(4)">
      <div class="cookingResult-wrap">
        <p class="cookingResult-msg" v-if="showCookingResult">{{cookingResult}}</p>
      </div>
    </div>
    <div class="recipe-group" v-show="showRecipe == 4">
      <div class="recipe-list">
        <div class="recipe-item" @click="cookingStart('fishSoup')">鮮魚湯</div>
        <div class="recipe-item" @click="cookingStart('handPulledNoodle')">拉麵</div>
      </div>
    </div>
    <div class="cooking-finish" v-if="showCookingFinish">
      <div class="cooking-Time">{{cookingTime}}</div>
      <div class="finish-Btn" @click="stockpotFinish">終</div>
    </div>
    <!-- <div>已放入食材：{{cookingFood}}</div> -->
  </div>
</template>


<script>
import $ from "jquery";
import { mapState, mapMutations } from "vuex";
export default {
  data() {
    return {
      cookingFood: [],
      cookingTime: 0,
      showCookingFinish: false,
      cookingtimeSwitch: true,
      cookingFoodName: "",
      foodPlural: "",
      cookingResult: "",
      showCookingResult: false
    };
  },
  computed: {
    ...mapState(["showRecipe"])
  },
  methods: {
    //食材放置事件監聽綁定
    addGredients() {
      let stockpotArea = document.querySelector("#stockpot-area");
      stockpotArea.addEventListener("drop", this.dropInstockpotArea);
      stockpotArea.addEventListener("dragenter", this.cancelDefault);
      stockpotArea.addEventListener("dragover", this.cancelDefault);
    },
    //食材放置事件效果
    dropInstockpotArea(e) {
      this.cancelDefault(e);
      let value = e.dataTransfer.getData("text/plain");
      this.setAddFoods(value);
      this.consumeFood(value);
      e.target.style.width = "100px";
      e.target.style.height = "100px";
      e.target.style.margin = "10px";
      setTimeout(() => {
        e.target.style.width = "90px";
        e.target.style.height = "90px";
        e.target.style.margin = "15px";
      }, 150);
    },
    //移除預設效果
    cancelDefault(e) {
      e.preventDefault();
      e.stopPropagation();
      return false;
    },
    //加入食材
    setAddFoods(foods) {
      this.cookingFood.push(foods);
    },
    //開始料理
    cookingStart(foodname) {
      if (this.cookingFood.length == 0) {
        return;
      }
      let vm = this;
      let stockpotHide = $("#stockpot-area");
      stockpotHide.siblings(".recipe-group").addClass("hide");
      this.showCookingFinish = true;
      this.cookingFoodName = foodname;
      if (this.cookingtimeSwitch) {
        vm.cookingTiming = setInterval(() => {
          this.cookingTime += 1;
          if (this.cookingTime >= 999) {
            clearInterval(vm.cookingTiming);
          }
        }, 1000);
        this.cookingtimeSwitch = false;
      }
    },
    //結束料理，判定料理內容
    stockpotFinish() {
      let stockpotHide = $("#stockpot-area");
      stockpotHide.siblings(".recipe-group").removeClass("hide");
      this.showCookingFinish = false;
      this.IsShowRecipe(0);

      switch (this.cookingFoodName) {
        case "fishSoup":
          this.cookingfishSoup();
          break;
        case "handPulledNoodle":
          this.cookinghandPulledNoodle();
          break;

        default:
          break;
      }
    },
    //料理鮮魚湯
    cookingfishSoup() {
      let vm = this;
      clearInterval(vm.cookingTiming);
      this.cookingtimeSwitch = true;

      if (
        this.cookingFood.includes("water") &&
        this.cookingFood.includes("fish") &&
        this.cookingFood.includes("oil") &&
        this.cookingFood.includes("salt") &&
        this.cookingFood.length == 4 &&
        this.cookingTime == 12
      ) {
        this.addFoodItem({
          type: "addFoodItem",
          name: this.cookingFoodName,
          num: 1
        });
        this.cookingFood = [];
        this.cookingTime = 0;
        this.showTheCookingResultMsg("鮮魚湯 +1");
      } else {
        this.cookingFood = [];
        this.cookingTime = 0;
        this.showTheCookingResultMsg("料理失敗");
      }
      this.setFoodMaterial();
    },
    //料理拉麵
    cookinghandPulledNoodle() {
      let vm = this;
      clearInterval(vm.cookingTiming);
      this.cookingtimeSwitch = true;

      if (this.cookingFood.includes("meat")) {
        this.foodNumberCount("meat");
      }

      if (
        this.cookingFood.includes("meat") &&
        this.cookingFood.includes("water") &&
        this.cookingFood.includes("rawNoodles") &&
        this.cookingFood.includes("soySauce") &&
        this.cookingFood.includes("salt") &&
        this.cookingFood.length == 6 &&
        this.foodPlural == 2 &&
        this.cookingTime == 24
      ) {
        this.addFoodItem({
          type: "addFoodItem",
          name: this.cookingFoodName,
          num: 1
        });
        this.cookingFood = [];
        this.cookingTime = 0;
        this.showTheCookingResultMsg("拉麵 +1");
      } else {
        this.cookingFood = [];
        this.cookingTime = 0;
        this.showTheCookingResultMsg("料理失敗");
      }
      this.setFoodMaterial();
    },
    foodNumberCount(foodName) {
      let Compute = this.cookingFood.map(item => {
        let num = 0;
        if (item == foodName) {
          num += 1;
        }
        return num;
      });
      let total = Compute.reduce((a, b) => a + b);
      this.foodPlural = total;
    },
    showTheCookingResultMsg(foodName) {
      this.cookingResult = foodName;
      this.showCookingResult = true;
      setTimeout(() => {
        this.showCookingResult = false;
      }, 1200);
    },
    ...mapMutations([
      "consumeFood",
      "IsShowRecipe",
      "setFoodMaterial",
      "addFoodItem"
    ])
  },

  mounted() {
    this.addGredients();
    var cookingTiming;
  }
};
</script>


<style lang="scss" scoped>
// 廚具
#stockpot-area {
  position: absolute;
  left: 350px;
  bottom: 125px;
  width: 90px;
  height: 90px;
  margin: 15px;
  background-image: url(./assets/img/stockpot.png);
  background-position: center center;
  background-size: contain;
  background-repeat: no-repeat;
  cursor: pointer;
  z-index: 100;
  .cookingResult-wrap {
    position: relative;
    .cookingResult-msg {
      position: absolute;
      top: 30px;
      right: 0;
      left: 0;
      font-size: 16px;
      color: #ffffff;
      text-shadow: 0 0 5px #000000, 0 0 5px #000000;
      text-align: center;
      animation-name: cookingResult;
      animation-duration: 1.2s;
      animation-fill-mode: forwards;
      animation-timing-function: ease-out;
    }
  }
}

@keyframes cookingResult {
  0% {
    top: 30px;
  }
  70% {
    top: -10px;
    opacity: 1;
  }
  100% {
    top: -10px;
    opacity: 0;
  }
}

.recipe-group.hide {
  display: none;
}

// 可料理餐點區塊-對話框
.recipe-group {
  position: absolute;
  width: 112px;
  background-color: rgba(0, 0, 0, 0.6);
  bottom: 118px;
  left: 350px;
  z-index: 100;
  border-radius: 10px;
  &::after {
    position: absolute;
    background: rgba(0, 0, 0, 0);
    z-index: 100;
    border-color: rgba(0, 0, 0, 0.6) rgba(0, 0, 0, 0.6) transparent transparent;
    border-style: solid solid solid solid;
    border-width: 8px 12px 8px 12px;
    content: "";
    top: -16px;
    height: 0px;
    left: 70px;
    width: 0px;
    transform: rotate(180deg);
  }
}

// 可料理餐點區塊-選項
.recipe-list {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 33px;
  .recipe-item {
    padding: 3px 4px;
    border: 1px solid #ffffff;
    color: #ffffff;
    margin: 0 3px;
    font-weight: bold;
    border-radius: 5px;
    font-size: 13px;
    cursor: pointer;
    transition: all 0.6s;
    &:hover {
      background-color: #fff;
      color: #000000;
    }
  }
}

// 料理計時框
.cooking-finish {
  position: absolute;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 33px;
  width: 80px;
  background-color: rgba(0, 0, 0, 0.6);
  bottom: 115px;
  left: 375px;
  z-index: 100;
  border-radius: 10px;
  &::after {
    position: absolute;
    background: rgba(0, 0, 0, 0);
    z-index: 100;
    border-color: rgba(0, 0, 0, 0.6) rgba(0, 0, 0, 0.6) transparent transparent;
    border-style: solid solid solid solid;
    border-width: 8px 8px 8px 8px;
    content: "";
    top: -16px;
    height: 0px;
    left: 20px;
    width: 0px;
    transform: rotate(180deg) scaleX(-1);
  }
  .finish-Btn {
    padding: 2px 4px;
    border: 1px solid #ffffff;
    color: #ffffff;
    margin-left: 5px;
    font-weight: bold;
    font-size: 14px;
    border-radius: 5px;
    cursor: pointer;
    transition: all 0.6s;
    &:hover {
      background-color: #fff;
      color: #000000;
    }
  }
  .cooking-Time {
    color: orange;
    width: 35px;
    font-size: 18px;
    text-align: center;
  }
}
</style>


