import Vue from 'vue';
import Vuex from 'vuex';

import allFoodData from './foodkind'

Vue.use(Vuex);
let cookingTiming;
const store = new Vuex.Store({
    state: {
        cookingFood: [],
        cookingTime: 0,
        cookingtimeSwitch: true,
        showRecipe: '',
        hasMoney: 300,
        Mistake: 0,
        foodItem: [],
        openRestaurant: '',
        theScore: 0,
    },
    mutations: {
        //初始化取得食材data
        setfoodsData(state) {
            state.foodItem = [...allFoodData];
        },
        // 顯示當前廚具可料理清單
        IsShowRecipe(state, number) {
            if (state.showRecipe !== number) {
                state.showRecipe = number;
            } else {
                state.showRecipe = '';
            }
        },
        // 每拖曳一份食材至烘焙作業區，將該食材的數量減1
        consumeFood(state, food) {
            for (let i = 0; i < state.foodItem.length; i++) {
                if (state.foodItem[i].food == food) {
                    state.foodItem[i].quantity -= 1;
                }
            }
        },
        //增加食材與料理數量
        addFoodItem(state, food) {
            for (let i = 0; i < state.foodItem.length; i++) {
                if (state.foodItem[i].food == food.name) {
                    state.foodItem[i].quantity += food.num;
                }
            }
        },
        // 設定食材圖片為可拖曳元素
        setFoodMaterial() {
            setTimeout(() => {
                let foodMaterial = document.querySelectorAll('.food-material');
                foodMaterial.forEach(element => {
                    element.addEventListener("dragstart", dragStoneStart);
                });

                function dragStoneStart(e) {
                    e.dataTransfer.setData("text/plain", e.target.id);
                }
            }, 50);
        },
        //購買食材
        buyTheFood(state, food) {
            for (let i = 0; i < state.foodItem.length; i++) {
                if (state.foodItem[i].food == food.foodName) {
                    if (state.hasMoney - food.foodPrice >= 0) {
                        state.foodItem[i].quantity += food.singleBuyNum;
                        state.hasMoney -= food.foodPrice;
                    } else {
                        alert('您的金錢不足唷！')
                    }
                }
            }
        },
        //獲得負評
        getBadAppraise(state) {
            state.Mistake += 1;
        },
        //獲得金錢/分數
        getMoney(state, money) {
            state.hasMoney += money;
            state.theScore += 100;
        },
        //判定遊戲是否結束
        judgmentGameOver(state) {
            if (state.Mistake > 3) {
                state.openRestaurant = 'close';
            } else {
                return;
            }
        },
        openTheShop(state) {
            state.openRestaurant = 'open';
        }
        //遊戲重新開始
        // againToGame(state) {
        //     state.cookingFood = [];
        //     state.cookingTime = 0;
        //     state.cookingtimeSwitch = true;
        //     state.showRecipe = '';
        //     state.hasMoney = 300;
        //     state.Mistake = 0;
        //     state.foodItem = [];
        //     state.openRestaurant = true;
        // },
    },
})

export default store;


