<template>
  <div>
    <div id="computer-area" @click="IsShowRecipe(6)"></div>
    <div class="recipe-group" v-show="showRecipe == 6">
      <div class="recipe-list">
        <div v-for="(item, index) in foodItem" :key="index">
          <div class="recipe-item" v-if="item.isCanBuy" @click="buy(item)">
            <div>{{item.foodname}}</div>
            <div class="recipe-item-price">${{item.price}}</div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>


<script>
import $ from "jquery";
import { mapState, mapMutations } from "vuex";
export default {
  computed: {
    ...mapState(["showRecipe", "foodItem"])
  },
  methods: {
    buy(value) {
      this.buyTheFood({
        type: "buyTheFood",
        foodName: value.food,
        foodPrice: value.price,
        singleBuyNum: value.singleBuyNum
      });
      this.setFoodMaterial();
    },
    ...mapMutations(["IsShowRecipe", "buyTheFood","setFoodMaterial"])
  }
};
</script>


<style lang="scss" scoped>
// 電腦位置
#computer-area {
  position: absolute;
  left: 530px;
  bottom: 165px;
  width: 80px;
  height: 80px;
  margin: 15px;
  background-image: url(./assets/img/computer.png);
  background-position: center center;
  background-size: contain;
  background-repeat: no-repeat;
  cursor: pointer;
  z-index: 99;
}

.recipe-group.hide {
  display: none;
}

// 叫貨區塊-對話框
.recipe-group {
  position: absolute;
  width: 633px;
  height: 150px;
  background-color: rgba(0, 0, 0, 0.7);
  bottom: 55%;
  padding: 10px;
  left: 0;
  right: 0;
  z-index: 100;
}

// 叫貨區塊-選項
.recipe-list {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  flex-wrap: wrap;
  .recipe-item {
    padding: 5px 8px;
    border: 1px solid #ffffff;
    color: #ffffff;
    margin: 8px 8px;
    font-weight: bold;
    border-radius: 5px;
    font-size: 15px;
    width: 105px;
    cursor: pointer;
    display: flex;
    justify-content: space-between;
    transition: all 0.6s;
    &:hover {
      background-color: #fff;
      color: #000000;
      .recipe-item-price {
        color: #000000;
      }
    }
    .recipe-item-price {
      color: orange;
      transition: all 0.6s;
    }
  }
}
</style>


