<template>
  <div v-if="openRestaurant == 'close'">
    <div class="gameOver-title">Game Over</div>
    <div class="btn-group">
      <div class="scoreStyle">得分：{{theScore}}</div>
      <div class="again-Btn" @click="again">重新開始</div>
    </div>
  </div>
</template>

<script>
import { mapState, mapMutations } from "vuex";
export default {
  computed: {
    ...mapState(["openRestaurant","theScore"])
    // ...mapState(["openRestaurant", "theScore"])
  },
  methods: {
    again() {
      window.location.reload();
      //   this.againToGame();
      //   this.setfoodsData();
      //   this.setFoodMaterial();
    },
    ...mapMutations(["setFoodMaterial", "againToGame", "setfoodsData"])
  }
};
</script>

<style lang="scss" scoped>
.gameOver-title {
  font-size: 70px;
  color: #ffffff;
  text-align: center;
  padding-top: 60px;
}
.btn-group {
  width: 433px;
  padding-top: 50px;
  margin: 0 100px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.again-Btn {
  color: #ffffff;
  border: 2px solid #ffffff;
  border-radius: 10px;
  padding: 10px 20px;
  cursor: pointer;
  transition: all 0.5s;
  &:hover {
    background-color: #ffffff;
    color: #000000;
  }
}
.scoreStyle {
  color: orange;
  padding: 10px 20px;
  margin-right: 20px;
  font-size: 30px;
}
</style>


