<template>
  <div class="resourcesBar-wrap">
    <div class="resourcesBar-item-angry">
      <div class="angryImgStyle">
        <img v-if="Mistake>0" src="./assets/img/angry.png" class="animated bounceIn" />
      </div>
      <div class="angryImgStyle">
        <img v-if="Mistake>1" src="./assets/img/angry.png" class="animated bounceIn" />
      </div>
      <div class="angryImgStyle">
        <img v-if="Mistake>2" src="./assets/img/angry.png" class="animated bounceIn" />
      </div>
    </div>
    <div class="resourcesBar-item-status">
      <div class="resourcesBar-item-score">
        <img class="resourcesBar-itemImgStyle" src="./assets/img/score.png" />
        <div class="resourcesBar-itemTextStyle">
          <p>{{theScore}}</p>
        </div>
      </div>
      <div class="resourcesBar-item-money">
        <img class="resourcesBar-itemImgStyle" src="./assets/img/money.png" />
        <div class="resourcesBar-itemTextStyle">
          <p>${{hasMoney}}</p>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { mapState } from "vuex";
export default {
  computed: {
    ...mapState(["hasMoney", "Mistake","theScore"])
  }
};
</script>

<style lang="scss" scoped>
@import "./assets/scss/animate.css";
.resourcesBar-wrap {
  width: 633px;
  height: 55px;
  background-color: rgba(0, 0, 0, 0.6);
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.resourcesBar-item-angry {
  width: 300px;
  height: 50px;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  margin-left: 10px;
}
.resourcesBar-item-money {
  width: 150px;
  height: 50px;
  display: flex;
  justify-content: flex-end;
  align-items: center;
  padding-right: 20px;
}
.angryImgStyle {
  width: 40px;
  height: 40px;
  margin: 5px;
  display: flex;
  justify-content: center;
  align-items: center;
  border: 1px dotted #ffffff;
  padding: 2px;
  border-radius: 5px;
}
.resourcesBar-item-status{
  display: flex;
  justify-content: flex-end;
  align-items: center;

}
.resourcesBar-itemImgStyle{
  width: 40px;
  height: 40px;
}
.resourcesBar-itemTextStyle {
  margin-left: 5px;
  color: #ffffff;
  font-size: 24px;
  font-weight: bold;
  width: 90px;
  text-align: right;
}
.resourcesBar-item-score{
  width: 130px;
  height: 50px;
  display: flex;
  justify-content: flex-end;
  align-items: center;
  padding-right: 20px;
  margin-right: 30px;
}
</style>


