<template>
  <div>
    <div id="foodSteamer-area" @click="IsShowRecipe(1)">
      <div class="cookingResult-wrap">
        <p class="cookingResult-msg" v-if="showCookingResult">{{cookingResult}}</p>
      </div>
    </div>
    <div class="recipe-group" v-show="showRecipe == 1">
      <div class="recipe-list">
        <div class="recipe-item" @click="cookingStart('rice')">米飯</div>
        <div class="recipe-item" @click="cookingStart('SoupBag')">湯包</div>
      </div>
    </div>
    <div class="cooking-finish" v-if="showCookingFinish">
      <div class="cooking-Time">{{cookingTime}}</div>
      <div class="finish-Btn" @click="foodSteamerFinish">終</div>
    </div>
    <!-- <div>已放入食材：{{cookingFood}}</div> -->
  </div>
</template>


<script>
import $ from "jquery";
import { mapState, mapMutations } from "vuex";
export default {
  data() {
    return {
      cookingFood: [],
      cookingTime: 0,
      showCookingFinish: false,
      cookingtimeSwitch: true,
      cookingFoodName: "",
      foodPlural: "",
      cookingResult: "",
      showCookingResult: false
    };
  },
  computed: {
    ...mapState(["showRecipe"])
  },
  methods: {
    //食材放置事件監聽綁定
    addGredients() {
      let foodSteamerArea = document.querySelector("#foodSteamer-area");
      foodSteamerArea.addEventListener("drop", this.dropInfoodSteamerArea);
      foodSteamerArea.addEventListener("dragenter", this.cancelDefault);
      foodSteamerArea.addEventListener("dragover", this.cancelDefault);
    },
    //食材放置事件效果
    dropInfoodSteamerArea(e) {
      this.cancelDefault(e);
      let value = e.dataTransfer.getData("text/plain");
      this.setAddFoods(value);
      this.consumeFood(value);
      e.target.style.width = "90px";
      e.target.style.height = "90px";
      e.target.style.margin = "10px";
      setTimeout(() => {
        e.target.style.width = "80px";
        e.target.style.height = "80px";
        e.target.style.margin = "15px";
      }, 150);
    },
    //移除預設效果
    cancelDefault(e) {
      e.preventDefault();
      e.stopPropagation();
      return false;
    },
    //加入食材
    setAddFoods(foods) {
      this.cookingFood.push(foods);
    },
    //開始料理
    cookingStart(foodname) {
      if (this.cookingFood.length == 0) {
        return;
      }
      let vm = this;
      let foodSteamerHide = $("#foodSteamer-area");
      foodSteamerHide.siblings(".recipe-group").addClass("hide");
      this.showCookingFinish = true;
      this.cookingFoodName = foodname;
      if (this.cookingtimeSwitch) {
        vm.cookingTiming = setInterval(() => {
          this.cookingTime += 1;
          if (this.cookingTime >= 999) {
            clearInterval(vm.cookingTiming);
          }
        }, 1000);
        this.cookingtimeSwitch = false;
      }
    },
    //結束料理，判定料理內容
    foodSteamerFinish() {
      let foodSteamerHide = $("#foodSteamer-area");
      foodSteamerHide.siblings(".recipe-group").removeClass("hide");
      this.showCookingFinish = false;
      this.IsShowRecipe(0);

      switch (this.cookingFoodName) {
        case "rice":
          this.cookingRice();
          break;
        case "SoupBag":
          this.cookingSoupBag();
          break;

        default:
          break;
      }
    },
    //料理米飯
    cookingRice() {
      let vm = this;
      clearInterval(vm.cookingTiming);
      this.cookingtimeSwitch = true;
      if (
        this.cookingFood.includes("rawRice") &&
        this.cookingFood.includes("water") &&
        this.cookingFood.length == 2 &&
        this.cookingTime == 12
      ) {
        this.addFoodItem({
          type: "addFoodItem",
          name: this.cookingFoodName,
          num: 3
        });
        this.cookingFood = [];
        this.cookingTime = 0;
        this.showTheCookingResultMsg("米飯 +3");
      } else {
        this.cookingFood = [];
        this.cookingTime = 0;
        this.showTheCookingResultMsg("料理失敗");
      }
      this.setFoodMaterial();
    },
    //料理湯包
    cookingSoupBag() {
      let vm = this;
      clearInterval(vm.cookingTiming);
      this.cookingtimeSwitch = true;
      if (
        this.cookingFood.includes("dough") &&
        this.cookingFood.includes("meat") &&
        this.cookingFood.includes("oil") &&
        this.cookingFood.length == 3 &&
        this.cookingTime == 8
      ) {
        this.addFoodItem({
          type: "addFoodItem",
          name: this.cookingFoodName,
          num: 1
        });
        this.cookingFood = [];
        this.cookingTime = 0;
        this.showTheCookingResultMsg("湯包 +1");
      } else {
        this.cookingFood = [];
        this.cookingTime = 0;
        this.showTheCookingResultMsg("料理失敗");
      }
      this.setFoodMaterial();
    },
    showTheCookingResultMsg(foodName) {
      this.cookingResult = foodName;
      this.showCookingResult = true;
      setTimeout(() => {
        this.showCookingResult = false;
      }, 1200);
    },
    ...mapMutations([
      "consumeFood",
      "IsShowRecipe",
      "setFoodMaterial",
      "addFoodItem"
    ])
  },
  mounted() {
    this.addGredients();
    var cookingTiming;
  }
};
</script>


<style lang="scss" scoped>
#foodSteamer-area {
  position: absolute;
  left: 50px;
  bottom: 138px;
  width: 80px;
  height: 80px;
  margin: 15px;
  background-image: url(./assets/img/foodSteamer.png);
  background-position: center center;
  background-size: contain;
  background-repeat: no-repeat;
  cursor: pointer;
  z-index: 100;
  .cookingResult-wrap {
    position: relative;
    .cookingResult-msg {
      position: absolute;
      top: 30px;
      right: 0;
      left: 0;
      font-size: 16px;
      color: #ffffff;
      text-shadow: 0 0 5px #000000, 0 0 5px #000000;
      text-align: center;
      animation-name: cookingResult;
      animation-duration: 1.2s;
      animation-fill-mode: forwards;
      animation-timing-function:ease-out;
    }
  }
}

@keyframes cookingResult {
  0% {
    top: 30px;
  }
  70% {
    top: -10px;
    opacity: 1;
  }
  100% {
    top: -10px;
    opacity: 0;
  }
}

.recipe-group.hide {
  display: none;
}

.recipe-group {
  position: absolute;
  width: 100px;
  background-color: rgba(0, 0, 0, 0.6);
  bottom: 125px;
  left: 42px;
  z-index: 100;
  border-radius: 10px;
  &::after {
    position: absolute;
    background: rgba(0, 0, 0, 0);
    z-index: 100;
    border-color: rgba(0, 0, 0, 0.6) rgba(0, 0, 0, 0.6) transparent transparent;
    border-style: solid solid solid solid;
    border-width: 8px 10px 8px 10px;
    content: "";
    top: -16px;
    height: 0px;
    left: 55px;
    width: 0px;
    transform: rotate(180deg);
  }
}

.recipe-list {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 33px;
  .recipe-item {
    padding: 3px 4px;
    border: 1px solid #ffffff;
    color: #ffffff;
    margin: 0 3px;
    font-weight: bold;
    border-radius: 5px;
    font-size: 13px;
    cursor: pointer;
    transition: all 0.6s;
    &:hover {
      background-color: #fff;
      color: #000000;
    }
  }
}

.cooking-finish {
  position: absolute;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 33px;
  width: 80px;
  background-color: rgba(0, 0, 0, 0.6);
  bottom: 125px;
  left: 30px;
  z-index: 101;
  border-radius: 10px;
  &::after {
    position: absolute;
    background: rgba(0, 0, 0, 0);
    z-index: 100;
    border-color: rgba(0, 0, 0, 0.6) rgba(0, 0, 0, 0.6) transparent transparent;
    border-style: solid solid solid solid;
    border-width: 8px 12px 8px 12px;
    content: "";
    top: -16px;
    height: 0px;
    left: 40px;
    width: 0px;
    transform: rotate(180deg) scaleX(-1);
  }
  .finish-Btn {
    padding: 2px 4px;
    border: 1px solid #ffffff;
    color: #ffffff;
    margin-left: 5px;
    font-weight: bold;
    border-radius: 5px;
    font-size: 14px;
    cursor: pointer;
    transition: all 0.6s;
    &:hover {
      background-color: #fff;
      color: #000000;
    }
  }
  .cooking-Time {
    color: orange;
    width: 35px;
    font-size: 18px;
    text-align: center;
  }
}
</style>


