<template>
  <div class="CustomerList-wrap" v-if="openRestaurant == 'open'">
    <div class="Customer-Item">
      <CustomerItemOne />
    </div>
    <div class="Customer-Item">
      <CustomerItemTwo />
    </div>
    <div class="Customer-Item">
      <CustomerItemThree />
    </div>
    <div class="Customer-Item">
      <CustomerItemFour />
    </div>
  </div>
</template>

<script>
import { mapState, mapMutations } from "vuex";
import CustomerItemOne from "./CustomerItemOne.vue";
import CustomerItemTwo from "./CustomerItemTwo.vue";
import CustomerItemThree from "./CustomerItemThree.vue";
import CustomerItemFour from "./CustomerItemFour.vue";
export default {
  computed: {
    ...mapState(["openRestaurant"])
  },
  components: {
    CustomerItemOne,
    CustomerItemTwo,
    CustomerItemThree,
    CustomerItemFour
  }
};
</script>

<style lang="scss" scoped>
.Customer-Item {
  height: 100px;
  width: 120px;
}
</style>



