<template>
  <div class="foods-wrap">
    <div class="food-prev" @click="foodPrev"></div>
    <div class="foods-main">
      <div class="foods">
        <div v-for="(item, index) in foodItem" :key="index">
          <div class="food-box" v-if="item.quantity > 0">
            <img :id="item.food" class="food-material" :src="item.foodImg" />
            <div class="food-item">
              <p>{{item.foodname}}</p>
              <p>X {{item.quantity}}</p>
            </div>
          </div>
        </div>
      </div>
    </div>
    <div class="food-next" @click="foodNext"></div>
  </div>
</template>


<script>
import { mapState } from "vuex";
export default {
  data() {
    return {
      move: 0
    };
  },
  computed: {
    ...mapState(["foodItem"])
  },
  methods: {
    foodPrev() {
      let foodsBar = document.querySelector(".foods");
      this.move += 450;
      if (this.move > 0) {
        this.move = 0;
      }
      foodsBar.style.transform = `translate3d(${this.move}px, 0px, 0px)`;
    },
    foodNext() {
      let foodsBar = document.querySelector(".foods");
      // if (this.foodItem.length <= 5) {
      //   return;
      // }
      this.move -= 450;
      let foodiTtemNum = document.querySelectorAll('.food-item');
      if (this.move <= -((foodiTtemNum.length / 5) * 450)) {
        this.move += 450;
      }
      foodsBar.style.transform = `translate3d(${this.move}px, 0px, 0px)`;
    }
  },

};
</script>


<style lang="scss">
.foods-wrap {
  position: absolute;
  width: 550px;
  height: 100px;
  bottom: 10px;
  right: 0;
  left: 0;
  margin: 0 auto;
  background-color: rgba(0, 0, 0, 0.6);
  border-radius: 10px;
  display: flex;
  justify-content: center;
  align-items: center;
}
.foods-main {
  width: 450px;
  overflow: hidden;
}

.foods {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  transition: all 1s;
}

.food-box {
  width: 70px;
  height: 80px;
  margin: 10px;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}
.food-item {
  width: 100%;
  display: flex;
  justify-content: space-around;
  align-items: center;
  padding: 5px 0;
  color: #ffffff;
  font-size: 12px;
}

.food-material{
  width: 70px;
  height: 64px;
}

.food-prev {
  position: relative;
  width: 30px;
  height: 34px;
  cursor: pointer;
  z-index: 1000;
  margin-right: 10px;
}

.food-prev::before {
  position: absolute;
  content: "";
  width: 2px;
  height: 20px;
  background: #d5d5d5;
  top: 0;
  left: 10px;
  transform: rotate(45deg);
}

.food-prev::after {
  position: absolute;
  content: "";
  width: 2px;
  height: 20px;
  background: #d5d5d5;
  bottom: 0;
  left: 10px;
  transform: rotate(-45deg);
}

.food-next {
  position: relative;
  width: 30px;
  height: 34px;
  cursor: pointer;
  z-index: 1000;
  margin-left: 10px;
}

.food-next::before {
  position: absolute;
  content: "";
  width: 2px;
  height: 20px;
  background: #d5d5d5;
  top: 0;
  left: 15px;
  transform: rotate(-45deg);
}

.food-next::after {
  position: absolute;
  content: "";
  width: 2px;
  height: 20px;
  background: #d5d5d5;
  bottom: 0;
  left: 15px;
  transform: rotate(45deg);
}

.food-next:hover::before,
.food-next:hover::after,
.food-prev:hover::before,
.food-prev:hover::after {
  background: #868686;
}
</style>


