<template>
  <div class="CustomerItemTwo-wrap" v-if="costomerVisit">
    <div
      class="CustomerItemTwo-area animated"
      :class="{'slideInUp':costomerStatus == 'visit','slideOutDown':costomerStatus == 'leave','customerAngry':customerAngry,'customer1':costomerRole==1,'customer2':costomerRole==2,'customer3':costomerRole==3,'customer4':costomerRole==4}"
    >
      <div class="customerResult-wrap">
        <!-- <p class="customerResult-msg">1000</p> -->
        <p class="customerResult-msg" v-if="showCustomerResult">金錢 +{{orderPrice}}</p>
      </div>
    </div>
    <div class="bubble" v-if="showBubbleContent =='orderFood'">
      <div
        class="orderFoodImg animated bounceIn"
        :style="`background-image: url(${orderFoodImg});`"
        :title="orderFoodName"
      ></div>
    </div>
    <div class="bubble" v-if="showBubbleContent =='dissatisfied'">
      <div class="dissatisfiedImg animated bounceIn"></div>
    </div>
    <div class="bubble" v-if="showBubbleContent =='heart'">
      <div class="heartImg animated bounceIn"></div>
    </div>
  </div>
</template>

<script>
import { mapState, mapMutations } from "vuex";
import allFoodData from "./foodkind";
export default {
  data() {
    return {
      costomerStatus: "",
      costomerVisit: false,
      showBubbleContent: "",
      orderFoodName: "",
      orderFoodImg: "",
      orderFood: "",
      orderPrice: "",
      costomerHasFood: "",
      customerResult: "",
      showCustomerResult: false,
      waitTimeNum: 0,
      customerAngry: false,
      conServeMeals: false,
      costomerRole: 0
    };
  },
  computed: {
    ...mapState(["Mistake"])
  },
  methods: {
    //料理放置事件監聽綁定
    addGredients() {
      let CustomerItemTwoArea = document.querySelector(".CustomerItemTwo-area");
      CustomerItemTwoArea.addEventListener(
        "drop",
        this.dropInCustomerItemTwoArea
      );
      CustomerItemTwoArea.addEventListener("dragenter", this.cancelDefault);
      CustomerItemTwoArea.addEventListener("dragover", this.cancelDefault);
    },
    //料理放置事件效果
    dropInCustomerItemTwoArea(e) {
      if (!this.conServeMeals) {
        return;
      }
      this.cancelDefault(e);
      let value = e.dataTransfer.getData("text/plain");
      this.deliveryOfMeal(value);
      this.consumeFood(value);
      e.target.style.width = "110px";
      e.target.style.height = "110px";
      e.target.style.margin = "5px";
      setTimeout(() => {
        e.target.style.width = "100px";
        e.target.style.height = "100px";
        e.target.style.margin = "10px";
      }, 150);
      this.showBubbleContent = "";
      this.conServeMeals = false;
      this.costomerResult();
    },
    //移除預設效果
    cancelDefault(e) {
      e.preventDefault();
      e.stopPropagation();
      return false;
    },
    // 顧客-隨機選擇餐點
    getOrderFoodItem() {
      let orderfoodNum = Math.floor(Math.random() * (26 - 19 + 1) + 19);
      setTimeout(() => {
        for (let i = 0; i < allFoodData.length; i++) {
          this.orderFoodName = allFoodData[orderfoodNum].foodname;
          this.orderFoodImg = allFoodData[orderfoodNum].foodImg;
          this.orderFood = allFoodData[orderfoodNum].food;
          this.orderPrice = allFoodData[orderfoodNum].price;
          this.showBubbleContent = "orderFood";
        }
        this.conServeMeals = true;
        this.waitTime();
      }, 2000);
    },
    //等候時間
    waitTime() {
      let vm = this;
      vm.waitTiming = setInterval(() => {
        this.waitTimeNum += 1;
        if (this.Mistake > 3) {
          clearInterval(vm.waitTiming);
          this.waitTimeNum = 0;
        }
        if (this.waitTimeNum > 100) {
          this.feelBad();
        }
      }, 1000);
    },
    //送餐給顧客
    deliveryOfMeal(foods) {
      this.costomerHasFood = foods;
    },
    //顧客用餐結果
    costomerResult() {
      let vm = this;
      clearInterval(vm.waitTiming);
      this.waitTimeNum = 0;
      setTimeout(() => {
        if (this.orderFood == this.costomerHasFood) {
          this.feelGood();
        } else {
          this.feelBad();
        }
      }, 1000);
    },
    //顧客用餐體驗良好
    feelGood() {
      let vm = this;
      this.showBubbleContent = "heart";
      clearInterval(vm.waitTiming);
      this.waitTimeNum = 0;
      setTimeout(() => {
        this.showBubbleContent = "";
      }, 1500);
      setTimeout(() => {
        this.showThecustomerResultMsg();
      }, 2000);
      setTimeout(() => {
        this.costomerStatus = "leave";
      }, 3500);
      setTimeout(() => {
        this.costomerComeIn();
      }, 6500);
    },
    //顧客用餐體驗不佳
    feelBad() {
      let vm = this;
      this.showBubbleContent = "dissatisfied";
      this.customerAngry = true;
      clearInterval(vm.waitTiming);
      this.waitTimeNum = 0;
      setTimeout(() => {
        this.showBubbleContent = "";
      }, 1500);
      setTimeout(() => {
        this.costomerStatus = "leave";
        this.getBadAppraise();
      }, 2000);
      setTimeout(() => {
        this.customerAngry = false;
        this.judgmentGameOver();
        this.costomerComeIn();
      }, 3500);
    },
    //顧客上門
    costomerComeIn() {
      if (this.Mistake <= 3) {
        let visitTime = Math.floor(Math.random() * (100 - 0 + 1) + 1);
        if (visitTime > 60) {
          this.costomerRoleChosen(); 
          this.costomerVisit = true;
          this.costomerStatus = "visit";
          setTimeout(() => {
            this.addGredients();
            this.getOrderFoodItem();
          }, 500);
          return;
        } else {
          setTimeout(() => {
            this.costomerComeIn();
          }, 8000);
        }
      }
    },
    //顧客角色
    costomerRoleChosen() {
      let roleNum = Math.floor(Math.random() * (4 - 1 + 1) + 1);
      this.costomerRole = roleNum;
    },
    showThecustomerResultMsg() {
      this.getMoney(this.orderPrice);
      this.showCustomerResult = true;
      setTimeout(() => {
        this.showCustomerResult = false;
      }, 1200);
    },
    ...mapMutations([
      "consumeFood",
      "getBadAppraise",
      "getMoney",
      "judgmentGameOver"
    ])
  },
  mounted() {
    setTimeout(() => {
     this.costomerComeIn();
    }, 1000);
    var waitTiming;
  }
};
</script>

<style lang="scss" scoped>
@import "./assets/scss/animate.css";
.CustomerItemTwo-wrap {
  position: relative;
  height: 100px;
  width: 120px;
}
.CustomerItemTwo-area {
  position: absolute;
  // background-image: url(./assets/img/customer.png);
  background-position: bottom center;
  background-size: contain;
  background-repeat: no-repeat;
  width: 100px;
  height: 100px;
  margin: 10px;
}

.CustomerItemTwo-area.customer1{
  background-image: url(./assets/img/customer1.png);
}
.CustomerItemTwo-area.customer2{
  background-image: url(./assets/img/customer2.png);
}
.CustomerItemTwo-area.customer3{
  background-image: url(./assets/img/customer3.png);
}
.CustomerItemTwo-area.customer4{
  background-image: url(./assets/img/customer4.png);
}

.CustomerItemTwo-wrap .CustomerItemTwo-area.customerAngry {
  background-image: url(./assets/img/customerAngry.png);
}
.bubble {
  position: absolute;
  background-image: url(./assets/img/bubble.png);
  background-position: bottom center;
  background-size: contain;
  background-repeat: no-repeat;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 80px;
  height: 80px;
  top: -65px;
  right: -20px;
  animation-name: moveAnimation;
  animation-duration: 2s;
  animation-iteration-count: infinite;
  animation-timing-function: ease-in-out;
}

@keyframes moveAnimation {
  0% {
    top: -65px;
  }
  50% {
    top: -55px;
  }
  100% {
    top: -65px;
  }
}
.orderFoodImg {
  // background-image: url(./assets/img/tamikoyaki.png);
  background-position: bottom center;
  background-size: contain;
  background-repeat: no-repeat;
  width: 60px;
  height: 60px;
}

.dissatisfiedImg {
  background-image: url(./assets/img/dissatisfied.png);
  background-position: bottom center;
  background-size: contain;
  background-repeat: no-repeat;
  width: 50px;
  height: 50px;
}

.heartImg {
  background-image: url(./assets/img/heart.png);
  background-position: bottom center;
  background-size: contain;
  background-repeat: no-repeat;
  width: 50px;
  height: 50px;
}

.customerResult-wrap {
  position: relative;
  .customerResult-msg {
    position: absolute;
    top: 20px;
    right: 0;
    left: 0;
    font-size: 16px;
    color: #ffffff;
    text-shadow: 0 0 5px #000000, 0 0 5px #000000;
    text-align: center;
    animation-name: customerResult;
    animation-duration: 1.2s;
    animation-fill-mode: forwards;
    animation-timing-function: ease-out;
  }
}

@keyframes customerResult {
  0% {
    top: 20px;
  }
  70% {
    top: -10px;
    opacity: 1;
  }
  100% {
    top: -10px;
    opacity: 0;
  }
}
</style>


