<template>
  <div>
    <div class="wrap" v-if="isNotUsePc">
      <div class="canteen">
        <GameOver class="gameOverStyle" />
        <ResourcesBar />
        <FoodSteamer />
        <Wok />
        <ChoppingBoard />
        <Stockpot />
        <RollSurface />
        <Computer />
        <LuckyCat />
        <div class="barCounter">
          <FoodstuffArea />
        </div>
        <CustomerList class="customerStyle" />
      </div>
    </div>
    <div class="phone-msg" v-if="!isNotUsePc">
      <p>很抱歉，目前頁面暫不支援行動載具，請改以PC裝置瀏覽，謝謝！</p>
    </div>
  </div>
</template>

<script>
import $ from "jquery";
import FoodstuffArea from "./FoodstuffArea";
import FoodSteamer from "./FoodSteamer";
import Wok from "./Wok";
import ChoppingBoard from "./ChoppingBoard";
import Stockpot from "./Stockpot";
import RollSurface from "./RollSurface";
import Computer from "./Computer";
import ResourcesBar from "./ResourcesBar";
import CustomerList from "./CustomerList";
import GameOver from "./GameOver";
import LuckyCat from "./LuckyCat";

import { mapState, mapMutations } from "vuex";
export default {
  data() {
    return {
      isNotUsePc:false,
    }
  },
  components: {
    FoodstuffArea,
    FoodSteamer,
    Wok,
    ChoppingBoard,
    Stockpot,
    RollSurface,
    Computer,
    ResourcesBar,
    CustomerList,
    GameOver,
    LuckyCat
  },
  methods: {
    userIsNotPc() {
      if (
        navigator.userAgent.match(/Android/i) ||
        navigator.userAgent.match(/webOS/i) ||
        navigator.userAgent.match(/iPhone/i) ||
        navigator.userAgent.match(/iPad/i) ||
        navigator.userAgent.match(/iPod/i) ||
        navigator.userAgent.match(/BlackBerry/i) ||
        navigator.userAgent.match(/Windows Phone/i)
      ) {
       this.isNotUsePc = false;
      } else {
        this.isNotUsePc = true;
      }
    },
    ...mapMutations([
      "setFoodMaterial",
      "setAddFoods",
      "consumeFood",
      "setfoodsData"
    ])
  },
  mounted() {
    this.userIsNotPc();
    this.setfoodsData();
    this.setFoodMaterial();
  }
};
</script>

<style lang="scss" >
@import "./assets/scss/reset";

.wrap {
  margin: calc((100vh - 512px) / 2) auto 0;
  width: 633px;
  user-select: none;
  position: relative;
}

.canteen {
  position: relative;
  width: 633px;
  height: 512px;
  background-position: center center;
  background-size: contain;
  background-repeat: no-repeat;
  background-image: url(./assets/img/bg.png);
  z-index: 90;
}

.customerStyle {
  position: absolute;
  top: 159px;
  left: 20px;
  width: 500px;
  height: 100px;
  display: flex;
  justify-content: flex-end;
  z-index: 70;
}

.barCounter {
  position: absolute;
  background-position: bottom center;
  background-size: contain;
  background-repeat: no-repeat;
  background-image: url(./assets/img/barCounter.png);
  bottom: 0;
  left: 0;
  width: 633px;
  height: 265px;
  z-index: 80;
}

.gameOverStyle {
  position: absolute;
  top: 100px;
  left: 0;
  width: 633px;
  height: 290px;
  background-color: rgba(0, 0, 0, 0.8);
  z-index: 110;
  display: flex;
  flex-direction: column;
}
.phone-msg{
font-size: 70px;
font-weight: bold;
width: 100vw;
height: 100vh;
display: flex;
justify-content: center;
align-items: center;
p{
  padding: 20px;
}
}
</style>



