<template>
  <div>
    <div id="computer-area" @click="IsShowRecipe(7)"></div>
    <div class="recipe-group" v-show="showRecipe == 7" :class="{'hide':IsOpenShop}">
      <div class="recipe-list">
        <div class="recipe-title">準備好要營業了嗎？</div>
        <div class="recipe-btn-group">
          <div class="recipe-item" @click="IsShowRecipe(0)">再等一下</div>
          <div class="recipe-item" @click="openShop">是的</div>
        </div>
      </div>
    </div>
  </div>
</template>


<script>
import $ from "jquery";
import { mapState, mapMutations } from "vuex";
export default {
  data() {
    return {
      IsOpenShop: false
    };
  },
  computed: {
    ...mapState(["showRecipe"])
  },
  methods: {
    openShop() {
      this.IsOpenShop = true;
      this.openTheShop();

    },
    ...mapMutations(["openTheShop","IsShowRecipe"])
  }
};
</script>


<style lang="scss" scoped>
// 招財貓位置
#computer-area {
  position: absolute;
  left: 490px;
  bottom: 200px;
  width: 70px;
  height: 70px;
  margin: 15px;
  background-image: url(./assets/img/luckyCat.png);
  background-position: center center;
  background-size: contain;
  background-repeat: no-repeat;
  cursor: pointer;
  z-index: 99;
}

.recipe-group.hide {
  display: none;
}

// 營業確認-對話框
.recipe-group {
  position: absolute;
  width: 230px;
  background-color: rgba(0, 0, 0, 0.7);
  bottom: 60%;
  padding: 10px;
  right: 80px;
  z-index: 100;
  border-radius: 15px;
  &::after {
    position: absolute;
    background: rgba(0, 0, 0, 0);
    z-index: 100;
    border-color: rgba(0, 0, 0, 0.6) rgba(0, 0, 0, 0.6) transparent transparent;
    border-style: solid solid solid solid;
    border-width: 12px 12px;
    content: "";
    top: 79px;
    height: 0px;
    right: 30px;
    width: 0px;
    transform: rotate(270deg) scaleY(-1);
  }
}

.recipe-title {
  color: #ffffff;
}

.recipe-btn-group {
  display: flex;
}

// 營業確認-選項
.recipe-list {
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
  flex-wrap: wrap;
  .recipe-item {
    padding: 5px 8px;
    border: 1px solid #ffffff;
    color: #ffffff;
    margin: 8px 8px;
    font-weight: bold;
    border-radius: 5px;
    font-size: 15px;
    width: 80px;
    text-align: center;
    cursor: pointer;
    display: flex;
    justify-content: center;
    transition: all 0.6s;
    &:hover {
      background-color: #fff;
      color: #000000;
      .recipe-item-price {
        color: #000000;
      }
    }
  }
}
</style>


